import PressQRCodeWeb from '../common/vue3/q-r-code';
import PressBarcodeWeb from '../common/vue3/bar-code';






import { getActClass } from './utils';


export default {
  name: 'PressActBarcodeDialog',
  options: {
    styleIsolation: 'shared',
  },
  components: {

    PressQRCodeWeb,
    PressBarcodeWeb,






  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false,
    },
    url: {
      type: String,
      default: '',
      required: false,
    },
    useTipClass: {
      type: Boolean,
      default: true,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    closeDialog() {
      this.$emit('update:show', false);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
